.statistics {
    width: 500px;
    margin: 0 auto;
    box-shadow: 1px 2px 3px 2px #d8d9da;
    margin-bottom: 15px;
    border-radius: 5px;
  }
  
  .title {
    padding: 50px 0;
    margin-top: 10px;
    text-align: center;
    text-transform: uppercase;
    font-size: 18px;
    color: #516272;
    font-weight: 600;
    background-color: #FFF;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  
  .stats-list {
    display: flex;
  }
  
  .item {
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    color: #FFF;
  }
  
  .stats-list .item:first-child {
    border-bottom-left-radius: 5px;
  }
  
  .stats-list .item:last-child {
    border-bottom-right-radius: 5px;
  }
  
  .percentage {
    font-size: 20px;
    font-weight: 600;
  }