.profile {
    width: 500px;
    margin: 60px auto 0 auto;
    background-color: #FFF;
    box-shadow: 1px 2px 3px 2px #d8d9da;
    border-radius: 5px;
  }
  
  
  .description {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
  }
  
  .avatar {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: rgb(197, 204, 210);
    margin-bottom: 35px;
  }
  
  .name {
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 13px;
    color: #122236;
  }
  
  .tag {
    font-size: 20px;
    color: #A1ABB6;
    margin-bottom: 13px;
  }
  
  .location {
    font-size: 20px;
    color: #A1ABB6;
  }
  
  .stats {
    display: flex;
    background-color: #F3F6F9;
    border-top: 1px solid #d8d9da;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  
  .stats li {
    width: calc(100% / 3);
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .stats li:not(.stats li:last-child) {
    border-right: 1px solid #d8d9da;
  }
  
  .label {
    font-size: 18px;
    color: #A1ABB6;
    margin-bottom: 8px;
  }
  
  .quantity {
    font-size: 20px;
    font-weight: 700;
  }