.transaction-history {
    width: 500px;
    margin: 0 auto;
    background-color: #FFF;
    margin-bottom: 20px;
    box-shadow: 1px 1px 2px 2px #c3c4c4;
  }
  
  .row {
    height: 40px;
  }
  
  .transaction-history-head .row {
    background-color: #00BCD5;
    color: #FFF;
  }
  
  .transaction-history-body .row:nth-child(even) {
    background-color: #ECF1F3;
  }
  
  .transaction-history td,
  .transaction-history th {
    vertical-align: middle;
  }
  
  .transaction-history th:not(.transaction-history th:last-child) {
    border-right: 1px solid #FFF;
  }
  
  .transaction-history td {
    text-align: center;
  }
  
  .transaction-history td:not(.transaction-history td:last-child) {
    border-right: 1px solid #dcdcdc;
  }